import React, { useState, useCallback } from "react";
import styled from "styled-components";

import Collapsible from "react-collapsible";

import Row from "./Row";
import Slider from "./Slider";

import getPdpSublayers from "../info/getPdpSublayers";
import getGpSublayers from "../info/getGpSublayers";

const LayersWrapper = styled.div`
  box-sizing: border-box;
  position: absolute;
  left: 56px;
  top: 120px;
  width: 400px;
  color: ${props => props.palette.text};
  font: 400 12px Arial;
  * {
    margin: 0;
    box-sizing: border-box;
  }
  .Collapsible {
    .is-open {
      div {
        .expandedIcon {
          transform: rotate(180deg);
          transition: transform 0.2s;
        }
      }
    }
    .is-closed {
      div {
        .expandedIcon {
          transform: rotate(0deg);
          transition: transform 0.2s;
        }
      }
    }
  }
  .container {
    max-height: 80vh;
    overflow-y: overlay;
    overflow-x: hidden;
    width: calc(100% + 5px);
    &::-webkit-scrollbar {
      background-color: ${props => props.palette.additional}
      width: 5px;
    }
    &::-webkit-scrollbar-thumb {
      background-color: ${props => props.palette.turq}
      width: 5px;
    }
  }
`;

const Trigger = styled.div`
  width: 100%;
  padding: 5px;
  font-weight: 600;
  background-color: ${props => props.palette.primary};
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid ${props => props.palette.secondary};
  svg {
    fill: ${props => props.palette.text};
  }
`;

const getFreeLandsTitles = () => {
  let freeLands = new Array(40);

  for (var i = 0; i < freeLands.length; i++) {
    freeLands[i] = {
      title: `  ${i + 1}`,
      name: `oz${i + 1}`
    };
  }

  return freeLands;
};

const LAYERS = region => [
  {
    title: " ",
    items: [
      {
        name: "lands",
        title: ""
      },
      { name: "arrest", title: " ", parent: "lands" },
      /*{
        name: "lands_label",
        title: " "
      },*/
      { name: "free", title: " " },
      {
        name: "req",
        title: "",
        sublayers: [
          { title: " ", name: "podano" },
          { title: "  ", name: "soglas" },
          { title: "  ", name: "vydano" },
          { title: "  ", name: "razrabot" },
          { title: "  ", name: "utver" },
          {
            title:
              "        . ",
            name: "podacha"
          },
          {
            title:
              "     .  ",
            name: "postan"
          }
        ]
      },
      { name: "eval", title: " ", sublayers: getFreeLandsTitles() }
    ]
  },
  {
    title: ", , ",
    items: [
      { name: "buildings", title: "" }
      // { name: "buildings_label", title: " " }
    ]
  },
  {
    title: " ",
    items: [
      { name: "redlines", title: " " },
      { name: "func", title: " " },
      {
        name: "pdp",
        title: "  ",
        sublayers:
          region && ["05085", "05252"].includes(region.regionCode + region.code)
            ? getPdpSublayers(region)
            : null
      },
      {
        name: "gp",
        title: " ",
        sublayers:
          region && ["05085", "05252"].includes(region.regionCode + region.code)
            ? getGpSublayers(region)
            : null
      },
      {
        name: "voz",
        title: " "
      },
      {
        name: "vop",
        title: " "
      },
      {
        name: "inv",
        title: " "
      }
    ]
  },
  {
    title: " ",
    items: [
      { name: "water", title: "" },
      { name: "gas", title: "" },
      { name: "storm_water", title: " " },
      { name: "con", title: "" },
      { name: "heat", title: "" },
      { name: "sewer", title: "" },
      { name: "electr", title: "" }
    ]
  },
  {
    title: "",
    items: [
      { name: "pch", title: " " },
      { name: "rst", title: " " },
      {
        name: "ugl",
        title: " ",
        sublayers: []
      }
    ]
  },
  {
    title: " ",
    radio: true,
    items: [
      { name: "base", title: " " },
      { name: "bing", title: " (Bing)" },
      { name: "garysh", title: " " },
      { name: "kazgeo", title: "" }
    ]
  }
];

export const Config = {
  visibleLayers: ["base"]
};

function EknMapLayers(props) {
  const [opacity, setOpacity] = useState({});
  const [visibility, setVisibility] = useState({
    base: true,
    lands: true,
    lands_label: true
  });
  const [sliderName, setSliderName] = useState(null);
  const [openSlider, setOpenSlider] = useState(false);
  const [expanded, setExpanded] = useState([
    true,
    true,
    true,
    true,
    true,
    true
  ]);
  const [viewportOffset, setViewportOffset] = useState(null);

  const handleOpacityChange = useCallback(
    (name, event) => {
      const x = [];
      const value = parseInt(event.target.value);
      x[name] = value;
      setOpacity(Object.assign([], opacity, x));
      props.setLayerOpacity(name, value);
    },
    [props, opacity]
  );

  const handleExpanded = useCallback(
    (index, value) => {
      expanded[index] = value;
      setExpanded(Object.assign({}, expanded));
    },
    [expanded]
  );

  const handleVisibilityChange2 = useCallback(
    (group, item) => {
      const x = [];
      if (group.radio) {
        group.items.forEach(value => {
          if (value.name !== item.name) {
            x[value.name] = false;
            props.showLayer(value.name, false);
          }
        });
      }
      const value = !visibility[item.name];
      x[item.name] = value;
      const newVisibility = Object.assign([], visibility, x);
      Config.visibleLayers = Object.keys(newVisibility).filter(
        key => newVisibility[key] === true
      );
      /*if (item.name === "buildings") {
        newVisibility["buildings_label"] = true;
      }*/
      setVisibility(newVisibility);
      props.showLayer(item.name, value);
      if (
        value &&
        (item.name === "bing" || item.name === "kazgeo" || item.id === "garysh")
      ) {
        props.setDark(true);
      } else if (value && item.name === "base") {
        props.setDark(false);
      }
    },
    [props, visibility]
  );

  const handleSliderClick = useCallback((name, e) => {
    setSliderName(name);
    setOpenSlider(true);
    setViewportOffset(e.target.getBoundingClientRect().top);
  }, []);

  const handleRefresh = () => {
    console.log("handleRefresh");
  };

  return (
    props.open && (
      <LayersWrapper palette={props.theme}>
        <div className="container">
          {openSlider && props.open && (
            <Slider
              theme={props.theme}
              handleCloseSlider={() => setOpenSlider(false)}
              defaultValue={opacity[sliderName] ? opacity[sliderName] : 100}
              handleSliderChange={handleOpacityChange.bind(this, sliderName)}
              viewportOffset={viewportOffset}
            />
          )}
          {LAYERS(props.region || null).map((group, i) => (
            <Collapsible
              key={i}
              transitionTime={200}
              trigger={
                <Trigger palette={props.theme}>
                  {group.title}
                  <svg
                    className="expandedIcon"
                    xmlns="http://www.w3.org/2000/svg"
                    width="18"
                    height="18"
                    viewBox="0 0 18 18"
                  >
                    <path d="M12.44 6.44L9 9.88 5.56 6.44 4.5 7.5 9 12l4.5-4.5z" />
                  </svg>
                </Trigger>
              }
              open={expanded[i]}
              onOpening={() => handleExpanded(i, true)}
              onClosing={() => handleExpanded(i, false)}
            >
              {group.items.map((item, j) => (
                <Row
                  key={j}
                  sublayers={item.sublayers}
                  theme={props.theme}
                  child={item.parent}
                  needsCheckboxes={item.name === "req"}
                  // Checkbox / Radio props
                  isCheckbox={!group.radio}
                  isChecked={
                    visibility[item.name] ? visibility[item.name] : false
                  }
                  checkHandler={
                    ["zo"].includes(item.name)
                      ? () => {}
                      : () => handleVisibilityChange2(group, item)
                  }
                  // HandleRefresh props
                  handleRefresh={handleRefresh}
                  // LayerIcon props
                  layerName={item.name}
                  layerText={item.title}
                  region={props.region}
                  // ListButton props
                  hasListButton={
                    ![
                      "redlines",
                      "func",
                      "base",
                      "bing",
                      "kazgeo",
                      "garysh",
                      "lands_label",
                      "buildings_label"
                    ].includes(item.name)
                  }
                  listButtonVisible={visibility[item.name]}
                  listButtonHandler={() =>
                    props.handleGetLayerObjects(item.name, visibility["arrest"])
                  }
                  // OpacityButton props
                  menuId={`menu${item.id}`}
                  handleOpacityButtonClick={e =>
                    handleSliderClick(item.name, e)
                  }
                  opacityButtonId={`opacity-button-${j}`}
                  tooltipType={props.dark ? "light" : "dark"}
                  opacityValue={Math.round(
                    opacity[item.name] ? opacity[item.name] : 100
                  )}
                />
              ))}
            </Collapsible>
          ))}
        </div>
      </LayersWrapper>
    )
  );
}

export default EknMapLayers;
