import React from "react";
import "./App.css";
import { AppContext } from "./context";
import EknMap from "./EknMap";
import { WKT } from "ol/format";

//window.GS_URL = "http://192.168.13.178:8080/geoserver";
window.GS_URL = `${window.location.protocol}//${window.location.host}/geoserver`;

//window.REST_URL = "http://192.168.13.178:8080/egkn/rest";
//window.REST_URL = "http://localhost:8080/egkn/rest";
window.REST_URL = `${window.location.protocol}//${window.location.host}/egkn/rest`;

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      regions: [],
      changeLayerVisibility: {},
      selection: undefined,
      standalone: props.id === 'gis1'
    };
    this.id = props.id;
  };
  
  componentDidMount() {
    fetch(`${window.REST_URL}/map/districts`)
      .then(response => response.json())
      .then(json => {
        this.setState({regions: json});
      })
      .catch(e => console.log(e));
  }

  render() {
    return (
      <AppContext.Provider value={this.regions}>
        <EknMap
          regions={this.state.regions}
          changeLayerVisibility={this.state.changeLayerVisibility} 
          selection={this.state.selection}
          standalone={this.state.standalone}
          selectDistrict={this.state.selectDistrict}
          selectCadnum={this.state.selectCadnum}
          updateSize={this.state.updateSize}
          uid={this.id} />
      </AppContext.Provider>
    );
  }

  //  

  updateSize() {
    this.setState(Object.assign({}, this.state, { updateSize: Date.now() }));
  }

  parseRequest(json) {
    Object.keys(json).forEach(funcName => {
      switch(funcName) {
        case 'showLayer':
          this.showLayer(json[funcName].layer, json[funcName].show);
          break;
        case 'selectObject':
          this.selectObject(json[funcName].wkt);
          break;
        case 'selectCadnum':
            this.selectCadnum(json[funcName].cadnum);
            break;
        case 'selectDistrict':
            this.selectDistrict(json[funcName].rnCode);
            break;
        default:
          break;
      }
    })
  }

  /**
   * 
   * @param {*} layerName -   (buildibgs, lands, redlines  ..)
   * @param {*} show - true or false
   * 
   * : showLayer('buildings', true)
   */
  showLayer(layerName, show) {
    let arr = {};
    arr[layerName] = show;
    this.setState(Object.assign({}, this.state, { changeLayerVisibility: arr}));
  }

  /**
   * 
   * @param {*} wkt 
   * : 'POLYGON((680767.21487425 5793487.61627134,680804.708877024 5793447.73074242,680786.613906474 5793446.80150074,681059.569243689 5793176.6141648,682930.390545765 5791186.44744925,682460.663224626 5790753.40854621,682460.791333795 5790753.26356394,682460.757955872 5790753.23280712,682670.191791482 5790516.2423368,680702.773294162 5789155.94611479,680262.615644875 5789653.60993824,680515.174795243 5789850.50117689,680398.739846767 5790032.30452798,680145.329920468 5790271.98644112,679670.183020981 5789848.32776223,679245.613904825 5790207.46773992,678380.748149366 5790863.172772,678112.003281343 5793081.06219,678093.783462244 5793231.42064128,680744.98967984 5793485.66973821,680745.087548049 5793485.49424938,680767.21487425 5793487.61627134))'
   */
  selectObject(wkt) {
    this.setState(Object.assign({}, this.state, { selection: new WKT().readGeometryFromText(wkt) }));
  }

  selectCadnum(cadnum) {
    this.setState(Object.assign({}, this.state, { selectCadnum: cadnum }));
  }

  selectDistrict(rnCode) {
    if (this.state.regions) {
      for (let i = 0; i < this.state.regions.length; i++) {
        const region = this.state.regions[i];
        for (let j = 0; j < region.districts.length; j++) {
          const district = region.districts[j];
          if (district.rn_code === rnCode) {
            this.setState(Object.assign({}, this.state, { selectDistrict: district }));
            return;
          }
        }
      }
    }
  }
}

export default App;
