import React from "react";
import styled from "styled-components";

const LayerIconComponent = styled.img`
  box-sizing: border-box;
  width: 16px;
  height: 16px;
`;

const LayerIcon = ({ layerName, region, isSublayer }) => {
  if (region) {
    if (isSublayer) {
      return (
        <LayerIconComponent
          src={`legend-img/layerIcons/${region.regionCode +
            region.code +
            layerName}.png`}
          alt=""
        />
      );
    } else {
      return (
        <LayerIconComponent
          src={`legend-img/layerIcons/${layerName}.png`}
          alt=""
        />
      );
    }
  } else {
    return (
      <LayerIconComponent
        src={`legend-img/layerIcons/${layerName}.png`}
        alt=""
      />
    );
  }
};

export default LayerIcon;
