import React from "react";
import styled from "styled-components";
import Collapsible from "react-collapsible";

const RegionSelect = styled.div`
  box-sizing: border-box;
  position: absolute;
  top: 46px;
  right: 46px;
  width: 250px;
  font: 400 12px Arial;
  oveflow: hidden;
  background-color: ${props => props.palette.primary};
  color: ${props => props.palette.text};
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  h4 {
    font: 600 12px Arial;
    padding: 5px;
  }
  p {
    font: 400 12px Arial;
    padding: 2px 5px;
    background-color: ${props => props.palette.secondary};
    margin: 0;
  }
  p:hover {
    background-color: ${props => props.palette.additional};
    cursor: pointer;
  }
  #container {
    max-height: 60vh;
    overflow-y: auto;
    overflow-x: hidden;
    &::-webkit-scrollbar {
      width: 5px;
      background-color: ${props => props.palette.additional};
    }
    &::-webkit-scrollbar-thumb {
      width: 5px;
      background-color: ${props => props.palette.primary};
    }
  }
  .Collapsible {
    .Collapsible__trigger {
      div {
        width: 250px;
        padding: 5px;
        font-weight: 600;
        background-color: ${props => props.palette.primary};
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid ${props => props.palette.secondary};
        svg {
          fill: ${props => props.palette.text};
        }
      }
    }
    .is-open {
      div {
        svg {
          transform: rotate(180deg);
          transition: transform 0.2s;
        }
      }
    }
    .is-closed {
      div {
        svg {
          transform: rotate(0deg);
          transition: transform 0.2s;
        }
      }
    }
  }
`;

export default function ChooseRegion(props) {
  return (
    <RegionSelect palette={props.theme}>
      <div id="container">
        {props.regions.map(region => (
          <Collapsible
            key={region.code}
            trigger={
              <div>
                {region.nameRu}
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="18"
                  height="18"
                  viewBox="0 0 18 18"
                >
                  <path d="M12.44 6.44L9 9.88 5.56 6.44 4.5 7.5 9 12l4.5-4.5z" />
                </svg>
              </div>
            }
            transitionTime={200}
            easing="ease-out"
          >
            {region.districts.map(district => (
              <p
                key={region.code + district.code}
                onClick={() => {props.setRegion(district); props.showLayer('lands', true)}}
              >
                {district.nameRu}
              </p>
            ))}
          </Collapsible>
        ))}
      </div>
    </RegionSelect>
  );
}
