import React from "react";
import ObjectsInfoTable from "./ObjectsInfoTable";
import NoObj from "./components/NoObj";

import styled from "styled-components";
import SearchBar from "./components/SearchBar";

const Wrapper = styled.div`
  box-sizing: border-box;
  position: absolute;
  left: 56px;
  top: 80px;
  width: 400px;
`;

function EknMapInfo(props) {
  const handleClose = () => {
    props.setOpen();
  };

  const nothingFound = () => {
    let resultEmpty = false;
    if (Object.keys(props.searchResult).length === 0) {
      resultEmpty = true;
      return resultEmpty;
    } else {
      for (let prop in props.searchResult) {
        resultEmpty = props.searchResult[prop].length === 0;
        if (!resultEmpty) {
          return resultEmpty;
        }
      }
    }
    return resultEmpty;
  };

  return (
    props.open && (
      <Wrapper onClose={handleClose}>
        <SearchBar
          handleSearch={props.handleSearch}
          theme={props.theme}
          dark={props.dark}
        />
        {nothingFound() && <NoObj theme={props.theme} />}
        {props.searchResult && Object.keys(props.searchResult).length > 0 && (
          <ObjectsInfoTable
            info={props.searchResult}
            map={props.map}
            lyers={props.layers}
            handleSelect={props.handleSelect}
            getObjects={props.getObjects}
            theme={props.theme}
            arrestIsChecked={props.arrestIsChecked}
            dark={props.dark}
          />
        )}
      </Wrapper>
    )
  );
}

export default EknMapInfo;
