import React from "react";
import styled from "styled-components";
import ReactTooltip from 'react-tooltip';

const Search = styled.div`
  input {
    box-sizing: border-box;
    height: 36px;
    background-color: ${props => props.palette.primary};
    color: ${props => props.palette.text};
    border: none;
    outline: none;
    width: 100%;
    padding: 8px;
    margin-bottom: 4px;
  }
  #search-tooltip {
    font-family: Arial;
    font-size: 12px;
    opacity: 1;
  }
`;



export default function SearchBar(props) {
  return (
    <Search
      palette={props.theme}
      data-tip
      data-for="search-tooltip"
    >
      <ReactTooltip id="search-tooltip" effect="solid" border type={props.dark ? "light" : "dark"}>: 12345689012  .  34</ReactTooltip>
      <input
        placeholder="     / "
        onKeyPress={e => {
          if (e.charCode === 13) {
            props.handleSearch(e.target.value);
          }
        }}
      />
    </Search>
  );
}
