import React, { useState } from "react";
import Collapsible from "react-collapsible";
import styled from "styled-components";

const Legend = styled.div`
  box-sizing: border-box;
  position: absolute;
  top: 160px;
  left: 56px;
  width: 250px;
  font: 400 12px Arial;

  background-color: ${props => props.palette.primary};
  color: ${props => props.palette.text};
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  h4 {
    font: 600 12px Arial;
    padding: 5px;
    &:hover {
      cursor: default;
    }
  }
  #container {
    max-height: 60vh;
    overflow-y: auto;
    overflow-x: hidden;
    &::-webkit-scrollbar {
      width: 5px;
      background-color: ${props => props.palette.additional};
    }
    &::-webkit-scrollbar-thumb {
      width: 5px;
      background-color: ${props => props.palette.primary};
    }
  }
  .Collapsible {
    .Collapsible__trigger {
      div {
        width: 250px;
        padding: 5px;
        font-weight: 600;
        background-color: ${props => props.palette.additional};
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid ${props => props.palette.secondary};
        svg {
          fill: ${props => props.palette.text};
        }
      }
    }
    .is-open {
      div {
        svg {
          transform: rotate(180deg);
          transition: transform 0.2s;
        }
      }
    }
    .is-closed {
      div {
        svg {
          transform: rotate(0deg);
          transition: transform 0.2s;
        }
      }
    }
  }
`;

const Row = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 2px 5px;
  background-color: ${props => props.palette.secondary};
  img {
    width: 24px;
  }
  p {
    margin-left: 5px;
    font: 400 12px Arial;
    &:hover {
      background-color: inherit;
      cursor: default;
    }
  }
`;

const ColorSquare = styled.div`
  box-sizing: border-box;
  width: 24px;
  height: 24px;
  background-color: ${props =>
    props.backgroundColor ? props.backgroundColor : "green"};
  border: 1px ${props => (props.borderStyle ? props.borderStyle : "solid")}
    ${props => (props.borderColor ? props.borderColor : props.backgroundColor)};
`;

const getValZones = () => {
  const zoneColors = [
    "#FFD9CA",
    "#CCFFEE",
    "#FFC1B5",
    "#FFB599",
    "#EDC3A5",
    "#FFDB99",
    "#FFEF99",
    "#FFFF99",
    "#FFFFF3",
    "#F1F1F1",
    "#FDFDED",
    "#FFF9EE",
    "#C2EBDD",
    "#FFF0E3",
    "#F8F6DD",
    "#F9F5D1",
    "#E5E2C4",
    "#F5F5FD",
    "#EFE5EF",
    "#F1D9F1",
    "#F8CDF8",
    "#E4D2D2",
    "#D3F8D3",
    "#D7EBAD",
    "#F0DBA6",
    "#D4C6F1",
    "#EBBEBE",
    "#FDCCC7",
    "#FF9999",
    "#D85BA9",
    "#FF5AB3",
    "#E69BB3",
    "#F7BF8F",
    "#DBC285",
    "#7F7FFF",
    "#7FDFDB",
    "#C5FF6D",
    "#4CFBB8",
    "#76CA9B",
    "#92B5F2"
  ];

  const valZonesArray = zoneColors.map((color, i) => ({
    name: `  ${i + 1}`,
    backgroundColor: color,
    borderColor: "#A4A4A4"
  }));

  return valZonesArray;
};

const LegendLib = [
  {
    name: "base",
    title: " ",
    items: [
      {
        name: "",
        backgroundColor: "#87CEFA"
      },
      {
        name: "  ",
        backgroundColor: "#90EE90"
      },
      {
        name: "",
        icon: "legend-img/streets.png",
        backgroundColor: "#87CEFA"
      },
      {
        name: " ",
        icon: "legend-img/railways.png",
        backgroundColor: "#87CEFA"
      },
      {
        name: "",
        icon: "legend-img/highways.png",
        backgroundColor: "#87CEFA"
      },
      {
        name: "",
        backgroundColor: "#FBEED5",
        borderColor: "#DFDBD1"
      }
    ]
  },
  {
    name: "oz",
    title: " ",
    items: getValZones()
  },
  {
    name: "planning",
    title: " ",
    items: [
      {
        name: " ",
        backgroundColor: "#FFEBD5"
      },
      {
        name: " .  . .",
        backgroundColor: "#F7F7F7"
      },
      {
        name: " . ",
        backgroundColor: "#F6DEFD"
      },
      {
        name: "- ",
        backgroundColor: "#FFD8CA"
      },
      {
        name: " ",
        backgroundColor: "#E2E2E2"
      },
      {
        name: " ",
        backgroundColor: "#EAF7E5"
      },
      {
        name: " ",
        backgroundColor: "#EDF9CC"
      },
      {
        name: "-. ",
        backgroundColor: "#EFD5CC"
      }
    ]
  }
];

export default function LegendWindow(props) {
  const [expanded, setExpanded] = useState({});

  const handleExpanded = (layer, value) => {
    const x = {};
    x[layer.name] = value;
    setExpanded(Object.assign({}, expanded, x));
  };

  return (
    props.open && (
      <Legend palette={props.theme}>
        <h4> </h4>
        <div id="container">
          {LegendLib.map(layer => (
            <Collapsible
              key={layer.name}
              trigger={
                <div>
                  {layer.title}
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="18"
                    height="18"
                    viewBox="0 0 18 18"
                  >
                    <path d="M12.44 6.44L9 9.88 5.56 6.44 4.5 7.5 9 12l4.5-4.5z" />
                  </svg>
                </div>
              }
              transitionTime={200}
              easing="ease-out"
              open={expanded[layer.name]}
              onOpening={() => handleExpanded(layer, true)}
              onClosing={() => handleExpanded(layer, false)}
            >
              {layer.items.map(item => (
                <Row key={item.name} palette={props.theme}>
                  {item.icon ? (
                    <img src={item.icon} alt="" />
                  ) : (
                    <ColorSquare
                      backgroundColor={item.backgroundColor}
                      borderColor={item.borderColor}
                      borderStyle={item.borderStyle}
                    />
                  )}
                  <p>{item.name}</p>
                </Row>
              ))}
            </Collapsible>
          ))}
        </div>
      </Legend>
    )
  );
}
