import React from "react";
import styled from "styled-components";
import ReactTooltip from "react-tooltip";

const OpacityButtonComponent = styled.button`
  border: none;
  outline: none;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: inherit;
  color: ${props => props.palette.text};
  svg {
    fill: ${props => props.palette.text};
    background-size: 12px 12px;
    background-position: center center;
    background-image: linear-gradient(to right, ${props => props.palette.primary}, ${props => props.palette.primary} ${props => props.opacityLevel}%, transparent ${props => props.opacityLevel}%, transparent);
    background-repeat: no-repeat;
  }
  svg::before {
    content: "";
    position: absolute;
    top: 0px;
    background: red;
    width: 20px;
    height: 20px;
  }
  .tooltip {
    padding: 2px 4px;
  }
  border-radius: 4px; 
  &:hover {
    background-color: ${props => props.palette.turq};
    cursor: pointer;
    div {
      border-color: ${props => props.palette.primary};
      cursor: pointer;
    }
  }
`;


const OpacityButton = ({theme, menuId, opacityButtonId, tooltipType, opacityValue, handleOpacityButtonClick}) => (
  <OpacityButtonComponent
    palette={theme}
    id={menuId}
    onClick={handleOpacityButtonClick}
    data-tip
    data-for={opacityButtonId}
    opacityLevel={opacityValue}
  >
    <ReactTooltip
      className="tooltip"
      type={tooltipType}
      border
      delayShow={250}
      id={opacityButtonId}
    >
       
    </ReactTooltip>
    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M20 15.31L23.31 12 20 8.69V4h-4.69L12 .69 8.69 4H4v4.69L.69 12 4 15.31V20h4.69L12 23.31 15.31 20H20v-4.69zM12 18c-3.31 0-6-2.69-6-6s2.69-6 6-6 6 2.69 6 6-2.69 6-6 6z"/></svg>
  </OpacityButtonComponent>
)

export default OpacityButton;