import React, { useState } from "react";
import styled from "styled-components";
import { chooseNCAStorageCall, findSubjectAttr } from "../NCALayerOps";
import { formatDate } from "../util";

const LoginWindow = styled.div`
  position: absolute;
  left: 37.5%;
  top: 30%;
  width: 400px;
  padding: 15px 30px 15px 20px;
  background-color: ${props => props.palette.primary};
  color: ${props => props.palette.text};
  & h1 {
    margin: 12px;
    font: 600 20px Arial;
  }
`;

const Row = styled.div`
  display: flex;
  margin: 2px;
  justify-content: space-between;
  align-items: center;
  & p {
    margin: 5px;
    font: 400 14px Arial;
  }
  & input, h3 {
    width: 70%;
    margin: 0;
    height: 18px;
    font: 400 14px Arial;
    background-color: ${props => props.palette.primary};
    color: ${props => props.palette.text};
    outline: none;
    border: none;
    border-bottom: 1px solid ${props => props.palette.text};
    }
  }
  & div {
    display: flex;
    overflow-x: hidden;
    width: 70%;
  }
`;

const ButtonContainer = styled.div`
  display: flex;
  justify-content: flex-end;
  align-items: center;
`;

const Button1 = styled.button`
  background-color: ${props => props.palette.additional};
  text-align: center;
  margin: 12px 25px 15px 5px;
  font: 600 14px Arial;
  padding: 10px;
  color: ${props => props.palette.text};
  border: none;
  outline: none;
  &:hover {
    background-color: ${props => props.palette.secondary};
    cursor: pointer;
  }
`;

const Button2 = styled.button`
  background-color: ${props => props.palette.additional};
  text-align: center;
  margin: 12px 0 15px 5px;
  font: 600 14px Arial;
  padding: 10px;
  color: ${props => props.palette.text};
  border: none;
  outline: none;
  &:hover {
    background-color: ${props => props.palette.secondary};
    cursor: pointer;
  }
`;

export default function LoginDialog(props) {
  const [userInfo, setUserInfo] = useState({});

  const handleLogin = () => {
    props.setOpenLogin(false);
    props.setUserInfo(userInfo);
  };

  const handleChooseCert = () => {
    chooseNCAStorageCall(result => {
      if (result) {
        if (result.code === "500") {
          if (result.message && result.message !== "action.canceled") {
            //openNcaLayerError();
          }
        } else if (result.code === "200") {
          const info = {};
          var responseObject = result.responseObject;
          var subjectDn = responseObject.subjectDn;
          var subjectAttrs = subjectDn.split(",");
          info.iin = findSubjectAttr(subjectAttrs, "SERIALNUMBER").substr(3);
          info.email = findSubjectAttr(subjectAttrs, "E");
          var cn = findSubjectAttr(subjectAttrs, "CN");
          cn = cn || "";
          var middleName = findSubjectAttr(subjectAttrs, "G");
          middleName = middleName || "";
          info.fullName = cn.concat(" ").concat(middleName);

          info.dateStart = formatDate(
            new Date(parseInt(responseObject.certNotBefore))
          );
          info.dateEnd = formatDate(
            new Date(parseInt(responseObject.certNotAfter))
          );

          setUserInfo(info);
        }
      }
    });
  };

  return (
    <LoginWindow palette={props.theme}>
      <h1></h1>
      <Row palette={props.theme}>
        <p></p>
        <h3>{userInfo.iin}</h3>
      </Row>
      <Row palette={props.theme}>
        <p>E-mail</p>
        <h3>{userInfo.email}</h3>
      </Row>
      <Row palette={props.theme}>
        <p></p>
        <h3>{userInfo.fullName}</h3>
      </Row>
      <Row palette={props.theme}>
        <p> </p>
        <div>
          <h3 style={{ marginRight: "10px" }}>{userInfo.dateStart}</h3>-
          <h3 style={{ marginLeft: "10px" }}>{userInfo.dateEnd}</h3>
        </div>
      </Row>
      <ButtonContainer palette={props.theme}>
        <Button1 palette={props.theme} onClick={handleChooseCert}>
           
        </Button1>
        <Button2 palette={props.theme} onClick={handleLogin}>
          
        </Button2>
      </ButtonContainer>
    </LoginWindow>
  );
}
