const OSKEMEN = "05085";
const SEMEI = "05252";

const getGpSublayers = region => {
  switch (region.regionCode + region.code) {
    case OSKEMEN:
      return getRegionSublayers(UK, "ukGP");
    case SEMEI:
      return getRegionSublayers(SEM, "semGP");
    default:
      return [];
  }
};

export default getGpSublayers;

const getRegionSublayers = (regionLayers, key) =>
  regionLayers.map((layer, index) => ({
    name: `${key + index}`,
    title: layer
  }));

