import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { WKT } from "ol/format";

const LandRequestDialog = styled.div`
  box-sizing: border-box;
  position: absolute;
  top: 46px;
  right: 46px;
  width: 250px;
  font: 400 12px Arial;
  color: ${props => props.palette.text};
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  form {
    padding: 6px;
    button {
      margin-top: 4px;
      padding: 0 4px;
    }
    background-color: ${props => props.palette.secondary};
  }

  #file-upload {
    height: 1px;
    width: 1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    z-index: -10;
    background-color: ${props => props.palette.secondary};
  }

  #file-upload-label {
    display:inline-block;
    font-size: 12px;
    padding: 5px;
    margin: 2px 4px 2px -2px;
    border-radius: 2px;
    border: none;
    outline: none;
    background-color: ${props => props.palette.additional};
    color: ${props => props.palette.text} !important;
    flex: 1;
    text-align: center;
    :hover {
      cursor: pointer;
      background-color: ${props => props.palette.turq};
      }
  }

  .table-row {
    padding: 5px;
    display: grid;
    grid-template-columns: 10% 40% 40% 10%;
    justify-content: space-between;
    align-items: center;
    overflow-x: hidden;
    * {
      margin: 2px;
    }
    input {
      background-color: ${props => props.palette.secondary};
      color: ${props => props.palette.text};
      border: none;
      outline: none;
      width: 12ch;
      border-bottom: 1px solid ${props => props.palette.additional};
      &::placeholder {
        color: ${props => props.palette.primary};
      }
      &:hover {
        border-bottom: 1px solid ${props => props.palette.text};
      }
      &:focus {
        border-bottom: 1px solid ${props => props.palette.text};
      }
    }
    svg {
      width: 100%;
      text-align: center;
      fill: ${props => props.palette.text};
      &:hover {
        background-color: ${props => props.palette.additional};
        cursor: pointer;
      }
    }
    background-color: ${props => props.palette.secondary};
    &.table-header {
      background-color: ${props => props.palette.primary};
      font-weight: 600;
    }
  }

  #scroll-wrapper {
    width: 100%;
    max-height: 204px;
    overflow: auto;
    scrollbar-color: ${props => props.palette.primary}
      ${props => props.palette.additional};
    &::-webkit-scrollbar {
      width: 5px;
      background-color: ${props => props.palette.additional};
    }
    &::-webkit-scrollbar-thumb {
      width: 5px;
      background-color: ${props => props.palette.primary};
    }
  }

  #table-body {
    background-color: ${props => props.palette.secondary};
  }

  .data-field {
    * {
      padding: 5px;
    }
    background-color: ${props => props.palette.secondary};
    div {
      background-color: ${props => props.palette.secondary};
      sup {
        padding: 1px;
      }
    }
    h3 {
      background-color: ${props => props.palette.primary};
    }
    p {
      display: inline-block;
    }
    a {
      color: ${props => props.palette.text} !important;
      float: right;
      &:visited {
        color: ${props => props.palette.primary};
      }
    }
    svg {
      padding: 0;
      fill: yellow;
      float: right;
    }
  }

  #buttons {
    background-color: ${props => props.palette.primary};
    padding: 5px;
    * {
      font-size: 12px;
      padding: 5px;
      margin: 0 10px;
      border: none;
      outline: none;
      background-color: ${props => props.disabled ? props.palette.turq : props.palette.additional};
      color: ${props => props.palette.text} !important;
      flex: 1;
      text-align: center;
    }
    *:hover {
      cursor: pointer;
      background-color: ${props => props.palette.secondary};
    }
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: stretch;
  }
`;

export default function RequestDialog(props) {
  const [coords, setCoords] = useState([]);
  const [newCoord, setNewCoord] = useState(["", ""]);
  const [area, setArea] = useState(0);
  const [errIntersect, setErrIntersect] = useState(0);
  const [cost, setCost] = useState(0.0);
  const [orderUrl, setOrderUrl] = useState();
  const [valuationUrl, setValuationUrl] = useState();
  const [fileName, setFileName] = useState();

  const handleChange = (event, i, j) => {
    coords[i][j] = event.target.value;
    setCoords(Object.assign([], coords));
    coords.push(coords[0]);
    props.features[0].getGeometry().setCoordinates([coords]);
    setArea(props.features[0].getGeometry().getArea());
  };

  const handleRemove = i => {
    coords.splice(i, 1);
    setCoords(Object.assign([], coords));
    coords.push(coords[0]);
    props.features[0].getGeometry().setCoordinates([coords]);
    setArea(props.features[0].getGeometry().getArea());
  };

  const handleRemoveAll = () => {
    setCoords([]);
    props.features[0].getGeometry().setCoordinates([]);
    setCost(0.0);
    setArea(0);
    setErrIntersect(0);
    setValuationUrl();
  };

  const handleChangeNew = (event, j) => {
    newCoord[j] = event.target.value;
    setNewCoord(Object.assign([], newCoord));
  };

  const handleAdd = () => {
    coords.push(newCoord);
    setCoords(Object.assign([], coords));
    coords.push(coords[0]);
    props.features[0].getGeometry().setCoordinates([coords]);
    setArea(props.features[0].getGeometry().getArea());
    setNewCoord(["", ""]);
  };

  useEffect(() => {
    if (props.features.length > 0) {
      setCoords(
        props.features[0]
          .getGeometry()
          .getCoordinates()[0]
          .slice(0, -1)
      );
      setArea(props.features[0].getGeometry().getArea());
    } else {
      setCoords([]);
      setNewCoord(["", ""]);
      setArea(0);
    }
  }, [props.features]);

  useEffect(() => {
    if (props.features.length > 0) {
      const wkt = new WKT().writeGeometry(props.features[0].getGeometry());
      fetch(window.REST_URL + "/map/intersections?geom=" + wkt, {
        method: "GET",
        mode: "cors",
        credentials: "include"
      })
        .then(response => response.json())
        .then(result => setErrIntersect(result.result));
    }
  }, [props.features]);

  useEffect(() => {
    if (props.features.length > 0) {
      const srid = 3857;
      const wkt = new WKT().writeGeometry(props.features[0].getGeometry());
      fetch(window.REST_URL + "/map/cost?geom=" + wkt, {
        method: "GET",
        mode: "cors",
        credentials: "include"
      })
        .then(response => response.json())
        .then(result => setCost(result.result));

      setOrderUrl(`${window.REST_URL}/map/report?srid=${srid}&geom=${wkt}`);
      setValuationUrl(
        `${window.REST_URL}/map/valuation?srid=${srid}&geom=${wkt}`
      );
    }
  }, [props.features]);

  const formatNumber = (n, dec) => {
    let numAsString = n
      .toFixed(dec)
      .toString()
      .replace(/\./, ",");
    if (/\d{4},/.test(numAsString)) {
      numAsString = numAsString.replace(/(\d)(?=\d{3},)/, "$1 ");
    }
    while (/^\d{4}/g.test(numAsString)) {
      numAsString = numAsString.replace(/(?=\d)(\d{3})(?=\s)/, " $1");
    }
    return numAsString;
  };

  const handleFileUpload = event => {
    const coordinateFile = event.target.files[0];
    let reader = new FileReader();
    reader.onload = e => {addCoordsFromFile(e.target.result); setFileName(coordinateFile.name)};
    reader.readAsText(coordinateFile);
  }

  const addCoordsFromFile = text => {
    const coords = text.split("\n").map(line => line.split(" ").map(str => parseFloat(str)));
    props.handleLoadFeature(coords);
  }

  return (
    props.open && (
      <LandRequestDialog palette={props.theme}>
        <form >
          <input type='file' id='file-upload' onChange={e => handleFileUpload(e)}></input>
          <label htmlFor="file-upload" id="file-upload-label"> </label>
          {fileName ? 
            <span>{fileName}</span> : null
          }
        </form>
        <div id="table">
          <div className="table-row table-header">
            <div>&nbsp;</div>
            <p>X-</p>
            <p>Y-</p>
            <div>
              {coords.length > 0 && (
                <svg
                  onClick={() => handleRemoveAll()}
                  xmlns="http://www.w3.org/2000/svg"
                  width="20"
                  height="20"
                  viewBox="0 0 24 24"
                >
                  <path d="M6 19c0 1.1.9 2 2 2h8c1.1 0 2-.9 2-2V7H6v12zM19 4h-3.5l-1-1h-5l-1 1H5v2h14V4z" />
                </svg>
              )}
            </div>
          </div>
          <div id="table-body">
            <div id="scroll-wrapper">
              {coords.map((coord, i) => (
                <div className="table-row" key={i}>
                  <p>{i + 1}</p>
                  <input
                    type="number"
                    step="1"
                    value={parseFloat(coord[0]).toFixed(2)}
                    onChange={e => handleChange(e, i, 0)}
                  />
                  <input
                    type="number"
                    step="1"
                    value={parseFloat(coord[1]).toFixed(2)}
                    onChange={e => handleChange(e, i, 1)}
                  />
                  <svg
                    onClick={() => handleRemove(i)}
                    xmlns="http://www.w3.org/2000/svg"
                    width="20"
                    height="20"
                    viewBox="0 0 24 24"
                  >
                    <path d="M6 19c0 1.1.9 2 2 2h8c1.1 0 2-.9 2-2V7H6v12zM19 4h-3.5l-1-1h-5l-1 1H5v2h14V4z" />
                  </svg>
                </div>
              ))}
            </div>
          </div>
        </div>
        <div className="table-row">
          <div>&nbsp;</div>
          <input
            placeholder=" X"
            type="number"
            step="1"
            value={newCoord[0]}
            onChange={e => handleChangeNew(e, 0)}
          />
          <input
            placeholder=" Y"
            type="number"
            step="1"
            value={newCoord[1]}
            onChange={e => handleChangeNew(e, 1)}
          />
          <svg
            onClick={handleAdd}
            xmlns="http://www.w3.org/2000/svg"
            width="24"
            height="24"
            viewBox="0 0 24 24"
          >
            <path d="M19 13h-6v6h-2v-6H5v-2h6V5h2v6h6v2z" />
          </svg>
        </div>
        <div className="data-field">
          <h3></h3>

          <div>
            {formatNumber(area, 2)} <sup>2</sup> ({formatNumber(area/10000, 4)} .)
          </div>
        </div>
        <div className="data-field">
          <h3> </h3>
          <p>{formatNumber(cost, 2)} </p>
          {
            valuationUrl && !errIntersect ?
            <a href={valuationUrl} target="_blank" rel="noopener noreferrer">
               
            </a> :
            null
          }
        </div>
        <div className="data-field error">
          {((errIntersect & 1) > 0 || (errIntersect & 2) > 0) && (
            <h3>
              
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="18"
                height="18"
                viewBox="0 0 18 18"
              >
                <path d="M.5 16h17L9 1 .5 16zm9.5-2H8v-2h2v2zm0-3H8V7h2v4z" />
              </svg>
            </h3>
          )}
          {(errIntersect & 1) > 0 && <p>   !</p>}
          {(errIntersect & 2) > 0 && <p>   !</p>}
          {(errIntersect & 4) > 0 && <p>   !</p>}
        </div>
        {
          (valuationUrl && !errIntersect) ?
          <div id="buttons">
            <a
            role="button"
            href={orderUrl}
            target="_blank"
            rel="noopener noreferrer"
            >
              
            </a>
          <button onClick={() => { props.handleSend('02036031190920191659', 1);} }> </button>
          </div> :
          null
        }
        
      </LandRequestDialog>
    )
  );
}
