var webSocket = null;
var heartbeatMsg = '--heartbeat--';
var heartbeatInterval = null;

var nca_callback = null;

//var keyType = 'AUTH';
var storageAlias = 'PKCS12';

function initNCALayer(callbackM) {
	webSocket = new WebSocket('wss://127.0.0.1:13579/');

	webSocket.onopen = function() {
		if (heartbeatInterval === null) {
			heartbeatInterval = setInterval(pingLayer, 1000);
		}
		if (callbackM) {
			callbackM();
		}
	};

	webSocket.onclose = function(event) {};

	webSocket.onmessage = function(event) {
		var result;
		var rw;
		if (event.data === heartbeatMsg) {
			return;
		}
		result = JSON.parse(event.data);
		rw = {
			result: result.result,
			secondResult: result.secondResult,
			errorCode: result.errorCode,
			code: result.code,
			responseObject: result.responseObject,
			message: result.message,
			getResult: function() {
				return this.result;
			},
			getSecondResult: function() {
				return this.secondResult;
			},
			getErrorCode: function() {
				return this.errorCode;
			},
			getMessage: function () {
				return this.message;
			},
			getResponseObject: function () {
				return this.responseObject;
			},
			getCode: function () {
				return this.code;
			}
		};
		if (nca_callback)
			nca_callback(rw);		
	};
	
	webSocket.onerror = function(event) {
		openNcaLayerError("   NCALayer!");
	}
}

export function chooseNCAStorageCall(callback) {
	if (webSocket === null || webSocket.readyState === 3 || webSocket.readyState === 2) {
		initNCALayer(function() {
			chooseNCAStorageCall(callback);
		});		
	} else {
		// signXmlNewCall(function(result) {
		// 	signXmlNewBack(result);
		// });
		getKeyInfo(function(result) {
			callback(result);
			//webSocket.close();
		});
	}	
}

function openNcaLayerError(message) {
	alert(message ? message : '');	
}

