import React from "react";
import styled, { keyframes } from "styled-components";

const fadeIn = keyframes`
  to {
    opacity: 1;
  }
`;

const SliderComponent = styled.div`
  position: absolute;
  width: 300px;
  padding: 6px;
  top: ${props => props.viewportOffset - 121}px;
  transition: top 0.3s;
  left: calc(100% + 5px);
  border-radius: 0 6px 6px 0;
  opacity: 0;
  animation: ${fadeIn} 0.3s forwards;
  background-color: ${props => props.palette.secondary};
  p {
    margin-top: 10px;
  }
  div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 4px 0;
    p {
      text-align: center;
    }
    input[type="range"] {
      -webkit-appearance: none;
      -moz-appearance: none;
      margin: 0 4px;
      width: 100%;
      background: transparent;
      outline: none;
      &::-webkit-slider-thumb {
        -webkit-appearance: none;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        margin-top: -4px;
        border: none;
        background-color: ${props => props.palette.turq};
      }
      &::-moz-range-thumb {
        -webkit-appearance: none;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        margin-top: -4px;
        border: none;
        background-color: ${props => props.palette.turq};
      }
      &::-webkit-slider-runnable-track {
        height: 2px;
        background-color: ${props => props.palette.additional};
      }
      &::-moz-range-track {
        height: 2px;
        background-color: ${props => props.palette.additional};
      }
    }
  }
`;

const CloseButton = styled.button`
  font-size: 12px;
  outline: none;
  border: none;
  padding: 2px 6px;
  float: right;
  margin: 2px;
  border-radius: 2px;
  text-transform: uppercase;
  font-weight: bold;
  background: ${props => props.palette.additional};
  color: ${props => props.palette.text};
  z-index: 10;
  &:hover {
    background: ${props => props.palette.turq};
  }
`;

const Slider = ({
  theme,
  handleCloseSlider,
  defaultValue,
  handleSliderChange,
  viewportOffset
}) => (
  <SliderComponent palette={theme} viewportOffset={viewportOffset}>
    <CloseButton onClick={handleCloseSlider} palette={theme}>
      X
    </CloseButton>
    <p>(%):</p>
    <div>
      <p>0</p>
      <input
        type="range"
        min="0"
        max="100"
        step="1"
        defaultValue={defaultValue}
        onChange={handleSliderChange}
      />
      <p>100</p>
    </div>
  </SliderComponent>
);

export default Slider;
