import React from "react";
import styled from "styled-components";

const ListButtonComponent = styled.button`
  visibility: ${props => (props.isVisible ? "visible" : "hidden")};
  text-decoration: underline;
  background-color: inherit;
  border: none;
  outline: none;
  padding: 5px;
  background-color: inherit;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: 4px;
  &:hover {
    background-color: ${props => props.palette.turq};
    cursor: pointer;
  }
  color: ${props => props.palette.text};
`;

const ListButton = ({ theme, listButtonHandler, listButtonVisible }) => (
  <ListButtonComponent
    palette={theme}
    onClick={listButtonHandler}
    isVisible={listButtonVisible}
  >
    
  </ListButtonComponent>
);

export default ListButton;
