import React from "react";
import styled from "styled-components";

const UserInfo = styled.div`
  position: absolute;
  right: 46px;
  top: 46px;
  box-sizing: border-box;
  padding: 8px;
  color: ${props => props.palette.text};
  text-align: end;
  font: 400 14px Arial;
  background-color: ${props => props.palette.additional};;
  * {
    margin: 0;
  }
  h2 {
    width: 100%;
    font: 400 14px Arial;
    padding-bottom: 6px;
    margin-bottom: 12px;
    border-bottom: 1px solid ${props => props.palette.text};
  }
  div {
    display: flex;
    margin: 2px;
    justify-content: space-between;
    p {
      font: 400 12px Arial;
    }
  }
  }
  button {
    color: ${props => props.palette.text};
    height: 25px;
    border: none;
    outline: none;
    background-color: inherit;
    text-decoration: underline;
    &:hover {
      cursor: pointer
    }

  }
`;

export default function UserProfile({ userInfo, handleLogout, theme }) {
  return (
    <UserInfo palette={theme}>
      <h2>{userInfo.fullName}</h2>
      <div>
        <p></p>
        <p>{userInfo.iin}</p>
      </div>{" "}
      <div>
        <p>Email</p>
        <p>{userInfo.email}</p>
      </div>
      <button
        href=""
        onClick={event => {
          handleLogout();
          event.preventDefault();
        }}
      >
        
      </button>
    </UserInfo>
  );
}
