import React from "react";
import logo from "../img/logo.png";

export default function Header({ dark }) {
  const textColor = dark ? "white" : "black";
  const outlineColor = dark ? "black" : "white";

  return (
    <header
      style={{
        position: "absolute",
        display: "flex",
        alignItems: "center",
        margin: 10,
        padding: 0,
        zIndex: 1
      }}
    >
      <img
        src={logo}
        alt=""
        style={{
          width: "2em",
          backgroundColor: "black",
          borderRadius: "50%",
          border: `1px solid white`,
          padding: 2,
          filter: `invert(${dark ? "100%" : "0%"})`
        }}
      />
      <span
        style={{
          fontFamily: "Calibri",
          fontSize: "18px",
          margin: 5,
          fontWeight: 600,
          color: textColor,
          textShadow: `-1px -1px 0 ${outlineColor},
                        1px -1px 0 ${outlineColor},
                        -1px 1px 0 ${outlineColor},
                        1px 1px 0 ${outlineColor}`
        }}
      >
          
      </span>
    </header>
  );
}
