import React from "react";

import styled from "styled-components";

import Checkbox from "./Checkbox";
import Radio from "./Radio";
import RefreshLayer from "./RefreshLayer";
import LayerIcon from "./LayerIcon";
import ListButton from "./ListButton";
import OpacityButton from "./OpacityButton";

const RowComponent = styled.div`
  padding: 2px;
  display: grid;
  grid-template-columns: ${props =>
    props.child || props.level2
      ? "7.5% 7.5% 7.5% 7.5% 37.5% 15% 7.5% 10%"
      : "7.5% 7.5% 7.5% 45% 15% 7.5% 10%"};
  align-items: center;
  background-color: ${props => props.palette.secondary};
  div:hover {
    cursor: default;
  }
`;

const CollapsibleRow = styled.div`
  .row {
    padding: 2px;
    display: grid;
    grid-template-columns: 7.5% 7.5% 7.5% 45% 15% 7.5% 10%;
    align-items: center;
    background-color: ${props => props.palette.secondary};
    div:hover {
      cursor: default;
    }
    .expanded-icon {
      fill: ${props => props.palette.text};
      transform: rotate(${props => (props.isExpanded ? "90deg" : "0deg")});
      transition: transform 0.5s;
    }
  }
  .expandable {
    overflow: hidden;
    max-height: ${props => (props.isExpanded ? "500vh" : 0)};
    transition: max-height 0.5s ease-in-out;
  }
`;

const LegendRow = styled.div`
  background-color: ${props => props.palette.secondary};
  color: ${props => props.palette.text};
  display: flex;
  align-items: center;
  padding: 2px;
  display: grid;
  grid-template-columns: 7.5% 7.5% 7.5% 70%;
`;

const Row = ({
  theme,
  sublayers,
  child,
  isCheckbox,
  isChecked,
  checkHandler,
  layerName,
  layerText,
  region,
  handleRefresh,
  hasListButton,
  listButtonHandler,
  listButtonVisible,
  menuId,
  handleOpacityButtonClick,
  opacityButtonId,
  tooltipType,
  opacityValue
}) => {
  const [expanded, setExpanded] = React.useState(false);
  return sublayers ? (
    <CollapsibleRow palette={theme} isExpanded={expanded}>
      <div className="row">
        <svg
          className="expanded-icon"
          onClick={() => setExpanded(!expanded)}
          xmlns="http://www.w3.org/2000/svg"
          width="20"
          height="20"
          viewBox="0 0 24 24"
        >
          <path d="M8.59 16.34l4.58-4.59-4.58-4.59L10 5.75l6 6-6 6z" />
        </svg>
        {isCheckbox ? (
          <Checkbox
            theme={theme}
            isChecked={isChecked}
            checkHandler={checkHandler}
          />
        ) : (
          <Radio
            theme={theme}
            isChecked={isChecked}
            checkHandler={checkHandler}
          />
        )}
        {[
          "req",
          "eval",
          "func",
          "pdp",
          "gp",
          "voz",
          "vop",
          "inv",
          "water",
          "gas",
          "storm_water",
          "con",
          "heat",
          "sewer",
          "electr",
          "pch",
          "rst",
          "ugl",
          "ugpol",
          "base",
          "bing",
          "garysh",
          "kazgeo"
        ].includes(layerName) ? (
          <div />
        ) : (
          <LayerIcon theme={theme} layerName={layerName} isSublayer={true} />
        )}
        <p>{layerText}</p>
        {hasListButton ? (
          <ListButton
            theme={theme}
            listButtonHandler={listButtonHandler}
            listButtonVisible={listButtonVisible}
          />
        ) : (
          <div />
        )}
        <RefreshLayer
          theme={theme}
          handleRefresh={handleRefresh}
          tooltipType={tooltipType}
        />
        <OpacityButton
          theme={theme}
          menuId={menuId}
          handleOpacityButtonClick={handleOpacityButtonClick}
          opacityButtonId={opacityButtonId}
          tooltipType={tooltipType}
          opacityValue={opacityValue}
        />
      </div>
      <div className="expandable">
        {sublayers.map((sublayer, i) => (
          <LegendRow palette={theme} key={i}>
            <div></div>
            <div></div>
            <LayerIcon
              theme={theme}
              layerName={sublayer.name}
              region={["pdp", "gp"].includes(layerName) ? region : null}
            />
            <p>{sublayer.title}</p>
          </LegendRow>
        ))}
      </div>
    </CollapsibleRow>
  ) : (
    <RowComponent palette={theme} child={child}>
      <div></div>
      {child ? <div></div> : null}
      {isCheckbox ? (
        <Checkbox
          theme={theme}
          isChecked={isChecked}
          checkHandler={checkHandler}
        />
      ) : (
        <Radio
          theme={theme}
          isChecked={isChecked}
          checkHandler={checkHandler}
        />
      )}
      {[
        "req",
        "eval",
        "func",
        "pdp",
        "gp",
        "voz",
        "vop",
        "inv",
        "water",
        "gas",
        "storm_water",
        "con",
        "heat",
        "sewer",
        "electr",
        "pch",
        "rst",
        "ugl",
        "ugpol",
        "base",
        "bing",
        "garysh",
        "kazgeo"
      ].includes(layerName) ? (
        <div />
      ) : (
        <LayerIcon theme={theme} layerName={layerName} isSublayer={false} />
      )}
      <p>{layerText}</p>
      {hasListButton ? (
        <ListButton
          theme={theme}
          listButtonHandler={listButtonHandler}
          listButtonVisible={listButtonVisible}
        />
      ) : (
        <div />
      )}
      <RefreshLayer theme={theme} handleRefresh={handleRefresh} />
      <OpacityButton
        theme={theme}
        menuId={menuId}
        handleOpacityButtonClick={handleOpacityButtonClick}
        opacityButtonId={opacityButtonId}
        tooltipType={tooltipType}
        opacityValue={opacityValue}
      />
    </RowComponent>
  );
};

export default Row;
