import React, { useState, useCallback } from "react";
import styled from "styled-components";

const BottomBar = styled.div`
  background-color: ${props => props.palette.additional};
  display: flex;
  justify-content: center;
  align-items: center;
  p {
    margin: 0 6px;
    display: inline-block;
    font: 400 12px Arial;
    color: ${props => props.palette.text};
  }
`;

const Button = styled.button`
  border: none;
  outline: none;
  cursor: pointer;
  padding: 2px 4px;
  margin: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  margin-left: 6px;
  text-transform: uppercase;
  background: ${props => props.palette.secondary};
  color: ${props => props.palette.text};
  &:hover {
    background: ${props => props.palette.turq};
  }
  svg {
    fill: ${props => props.palette.text};
  }
`;

const PageNav = props => {
  const [pageNum, setPageNum] = useState(0);

  const handleNextPage = useCallback(() => {
    props.getObjects.func(pageNum + 1);
    setPageNum(pageNum + 1);
  }, [props, pageNum]);

  const handlePrevPage = useCallback(() => {
    props.getObjects.func(pageNum - 1);
    setPageNum(pageNum - 1);
  }, [props, pageNum]);

  return (
    <BottomBar palette={props.theme}>
      {pageNum > 0 && (
        <Button onClick={handlePrevPage} palette={props.theme}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="24"
            height="24"
            viewBox="0 0 24 24"
          >
            <path d="M15.41 16.09l-4.58-4.59 4.58-4.59L14 5.5l-6 6 6 6z" />
          </svg>
        </Button>
      )}
      <p> {pageNum + 1}</p>
      <Button onClick={handleNextPage} palette={props.theme}>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="24"
          height="24"
          viewBox="0 0 24 24"
        >
          <path d="M8.59 16.34l4.58-4.59-4.58-4.59L10 5.75l6 6-6 6z" />
        </svg>
      </Button>
      {/*
      <Button palette={props.theme}>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="24"
          height="24"
          viewBox="0 0 24 24"
        >
          <path d="M5.59 7.41L10.18 12l-4.59 4.59L7 18l6-6-6-6zM16 6h2v12h-2z" />
        </svg>
      </Button>
      */}
    </BottomBar>
  );
};

export default PageNav;
