import React from "react";
import styled from "styled-components";
import ReactTooltip from "react-tooltip";

const ButtonComp = styled.button`
  position: absolute;
  box-sizing: border-box;
  padding: 5px;
  * {
      margin: 0;
  }
  display: flex;
  justify-content: center;
  align-items: center;
  width: ${props => (props.title ? "auto" : "36px")};
  height: 36px;
  border: none;
  border-radius: 4px;
  outline: none;
  background-color: ${props => props.palette.additional};
  color: ${props => props.palette.text}
  &:hover {
    background-color: ${props => props.palette.turq};
  }
  svg {
      fill: ${props => props.palette.text};
  }
  .tooltip {
    opacity: 1;
    font-size: 12px;
  }
`;

export default function Button(props) {
  return (
    <ButtonComp
      onClick={props.handleClick}
      palette={props.theme}
      style={positionButton(props.tooltipText)}
      tooltipText={props.tooltipText}
      data-tip
      data-for={props.dataFor}
      title={props.title}
    >
      {props.title ? (
        <p>{props.title}</p>
      ) : (
        displaySvg(props.tooltipText, props.userInfo)
      )}
      <ReactTooltip
        className="tooltip"
        delayShow={250}
        id={props.tooltipText}
        effect="solid"
        border={true}
        type={props.tooltipDark ? "light" : "dark"}
      >
        {props.tooltipText}
      </ReactTooltip>
    </ButtonComp>
  );
}

