import React, { useState } from "react";
import { WKT } from "ol/format";

import styled from "styled-components";
import Collapsible from "react-collapsible";
import { Properties as LayerProps } from "./components/info/config";

import PageNav from "./components/PageNav";
import ReactTooltip from "react-tooltip";

const FeaturesWrapper = styled.div`
#container {
  max-height: 70vh;
  overflow-y: auto;
  overflow-x: hidden;
  width: calc(100% + 5px);
  &::-webkit-scrollbar {
    background-color: ${props => props.palette.additional}
    width: 5px;
  }
  &::-webkit-scrollbar-thumb {
    background-color: ${props => props.palette.turq}
    width: 5px;
  }
}

`;

const CollapsibleContainer = styled.div`
  background-color: ${props => props.palette.primary};
  * {
    margin: 0;
  }
  color: ${props => props.palette.text};
  font: 400 12px Arial;
  width: 400px;
  min-width: 400px;
  .row-wrapper {
    background-color: ${props => props.palette.additional};
    padding-bottom: 12px;
  }
  .Collapsible__contentOuter {
    background: ${props => props.palette.additional};
  }
`;

const Trigger = styled.div`
  display: grid;
  grid-template-columns: 10% 75% 15%;
  align-items: center;
  box-sizing: border-box;
  padding: 5px 8px 5px 8px;
  height: 55px;
  background-color: ${props =>
    props.expanded ? props.palette.additional : props.palette.primary};
  * {
    margin: 2px;
  }
  p:first-child {
    font-size: 14px;
    text-align: center;
  }
  border-top: 1px solid ${props => props.palette.additional};
  &:hover {
    background: ${props => props.palette.additional};
`;

const ButtonsAndGoTo = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  .tab-buttons {
    display: flex;
    overflow-x: auto;
    padding-right: 5px;
    &::-webkit-scrollbar {
      background-color: ${props => props.palette.accent}
      height: 5px;
      border-radius: 2px;
    }
    &::-webkit-scrollbar-thumb {
      background-color: ${props => props.palette.turq}
      height: 5px;
      border-radius: 2px;
    }
  }
`;

const Download = styled.div`
  margin: 0 6px;
  padding: 5px 8px 5px 8px;
  text-align: right;
  a {
    color: ${props => props.palette.text} !important;
    text-align: right;
    &:visited {
      color: ${props => props.palette.primary};
    }
  }
`;

const Row = styled.div`
  display: grid;
  box-sizing: border-box;
  grid-template-columns: 50% 45%;
  grid-gap: 5px;
  background: ${props => props.palette.secondary};
  margin: 0 6px;
  color: ${props => props.palette.text};
  font-style: normal;
  padding: 5px 8px 5px 8px;
  border-bottom: 1px solid ${props => props.palette.additional};
  &:last-of-type {
    border-radius: 0 0 5px 5px;
  }
  p {
    font-size: 14px;
    font-family: Calibri;
  }
  p:first-of-type {
    font-style: italic;
  }
  .doc-link {
    color: ${props => props.palette.text} !important;
    float: right;
    &:visited {
      color: ${props => props.palette.primary};
  }
`;

const GroupName = styled.div`
  background: ${props => props.palette.secondary};
  p {
    margin: 0 12px;
  }
  margin: 0px 6px 0px 6px;
  color: ${props => props.palette.text};
  font-size: 16px;
  padding: 8px 5px 4px 24px;
  border-bottom: 1px solid ${props => props.palette.accent};
`;

const Tabs = styled.div`
  background-color: ${props => props.palette.additional};
  margin: 12px 6px;
  padding: 6px;
  border: 1px solid ${props => props.palette.secondary};
  border-radius: 5px;
  svg {
    fill: ${props => props.palette.text};
    float: right;
    width: 20px;
    padding: 2px 5px;
    margin: 5px 5px 10px 5px;
    background: ${props => props.palette.secondary};
    border-radius: 5px;
    :hover {
      cursor: pointer;
      background: #2ea297;
    }
  }
  .group-wrapper {
    padding-bottom: 6px;
    border-top: 1px solid ${props => props.palette.additional};
    &:first-of-type {
      margin-top: 10px;
    }
  }
  .passport {
    margin: 6px;
    padding: 5px 8px 5px 8px;
    text-align: right;
    font-style: underline;
  }
`;

const Line = styled.div`
  height: 4px;
  width: 80%;
  margin: 0 6px;
  background-color: ${props => props.palette.additional};
`;

const Button = styled.button`
  font-size: 12px;
  outline: none;
  border: none;
  cursor: pointer;
  padding: 8px 12px;
  border-radius: 5px;
  margin: 5px 5px 10px 0px;
  text-transform: uppercase;
  font-weight: bold;
  background: ${props =>
    props.isActive ? props.palette.turq : props.palette.secondary};
  background: ${props => (props.isArrestButton ? "#660000" : null)};
  background: ${props =>
    props.isArrestButton && props.isActive ? "#990000" : null};
  color: ${props => props.palette.text};
  &:hover {
    background: ${props => props.palette.turq};
    background: ${props => (props.isArrestButton ? "#990000" : null)};
  }
  .react-tooltip {
    text-transform: none;
  }
`;

function ObjectsInfoTable(props) {
  const [activeTab, setActiveTab] = useState();
  const [activeObj, setActiveObj] = useState();

  const getTabContent = (objProps, layerName, obj) => {
    if (activeTab) {
      const items = activeTab.getItems
        ? activeTab.getItems(objProps)
        : [objProps];
      if (
        objProps.owners &&
        !objProps.owners.length &&
        activeTab.getItems &&
        activeTab.getTitle().props.children === ""
      ) {
        return (
          <GroupName palette={props.theme}>
               
          </GroupName>
        );
      } else if (
        objProps.obrems &&
        !objProps.obrems.length &&
        activeTab.getItems &&
        activeTab.getTitle().props.children === ""
      ) {
        return (
          <GroupName palette={props.theme}>
               
          </GroupName>
        );
      } else if (
        objProps.docs &&
        !objProps.docs.length &&
        activeTab.getItems &&
        activeTab.getTitle().props.children === ""
      ) {
        return (
          <GroupName palette={props.theme}> </GroupName>
        );
      } else {
        return items.map(item =>
          activeTab.groups.map((group, i) => (
            <div className="group-wrapper" key={i}>
              {group.getTitle ? (
                <GroupName palette={props.theme}>
                  <p>{group.getTitle(item)}</p>
                </GroupName>
              ) : (
                <Line palette={props.theme} />
              )}
              {item["owner_type_person_id"] === "001" ? (
                <Row palette={props.theme}> </Row>
              ) : (
                Object.keys(group.props).map(key => {
                  return (
                    <Row palette={props.theme} key={key}>
                      <p>
                        {key === "file_name" && objProps.docs.length
                          ? objProps.docs[0].file_name
                          : group.props[key]
                          ? group.props[key][0] || group.props[key]
                          : key}
                      </p>
                      <p>
                        {LayerProps.formatValue(
                          item[key],
                          group.props[key][1],
                          objProps.docs && objProps.docs.length
                            ? `${window.REST_URL}/map/passport?cadnum=${objProps.docs[0].id}`
                            : null
                        )}
                      </p>
                    </Row>
                  );
                })
              )}
            </div>
          ))
        );
      }
    }
  };

  const displayButtons = (layerName, obj, arrestsChecked) => {
    if (layerName !== "lands") {
      return LayerProps[layerName].tabs.map(
        (tab, i) =>
          (!tab.getItems || tab.getItems(obj.properties)) && (
            <Button
              onClick={() => setActiveTab(tab)}
              palette={props.theme}
              isActive={activeTab === tab}
              key={i}
              data-tip
              data-for={`button-tooltip-${i}`}
            >
              <ReactTooltip
                className="react-tooltip"
                id={`button-tooltip-${i}`}
                delayShow={250}
                type={props.dark ? "light" : "dark"}
              >
                  "{tab.getTitle()}"
              </ReactTooltip>
              {tab.getTitle()}
            </Button>
          )
      );
    } else if (layerName === "lands") {
      const hasArrests = Boolean(obj.properties.arrests.length);
      if (!hasArrests || !arrestsChecked) {
        return LayerProps[layerName].tabs
          .filter((tab, i) => i !== 2)
          .map(
            (tab, i) =>
              (!tab.getItems || tab.getItems(obj.properties)) && (
                <Button
                  onClick={() => setActiveTab(tab)}
                  palette={props.theme}
                  isActive={activeTab === tab}
                  key={i}
                  data-tip
                  data-for={`button-tooltip-${i}`}
                >
                  <ReactTooltip
                    className="react-tooltip"
                    id={`button-tooltip-${i}`}
                    delayShow={250}
                    type={props.dark ? "light" : "dark"}
                  >
                      "{tab.getTitle()}"
                  </ReactTooltip>
                  {tab.getTitle()}
                </Button>
              )
          );
      } else {
        return LayerProps[layerName].tabs.map(
          (tab, i) =>
            (!tab.getItems || tab.getItems(obj.properties)) && (
              <Button
                onClick={() => setActiveTab(tab)}
                palette={props.theme}
                isActive={activeTab === tab}
                key={i}
                isArrestButton={i === 2}
                data-tip
                data-for={`button-tooltip-${i}`}
              >
                <ReactTooltip
                  className="react-tooltip"
                  id={`button-tooltip-${i}`}
                  delayShow={250}
                  type={props.dark ? "light" : "dark"}
                >
                    "{tab.getTitle()}"
                </ReactTooltip>
                {tab.getTitle()}
              </Button>
            )
        );
      }
    }
  };

  function createObject(layerName, obj, i) {
    return (
      <CollapsibleContainer palette={props.theme} key={obj.id}>
        <Collapsible
          transitionTime={200}
          onOpening={() => {
            setActiveObj(obj);
            setActiveTab(LayerProps[layerName].tabs[0]);
          }}
          onClosing={() => {
            if (obj === activeTab) {
              setActiveObj(undefined);
            }
          }}
          open={obj === activeObj}
          trigger={
            <Trigger
              palette={props.theme}
              expanded={obj === activeObj}
              data-tip
              data-for="learn-more"
            >
              <p></p>
              <p>
                {LayerProps[layerName] && LayerProps[layerName].getTitle
                  ? LayerProps[layerName].getTitle(obj.properties)
                  : obj.id}
              </p>
              <ReactTooltip
                id="learn-more"
                delayShow={250}
                type={props.dark ? "light" : "dark"}
              >
                 
              </ReactTooltip>
            </Trigger>
          }
        >
          {obj === activeObj && (
            <Tabs palette={props.theme}>
              <ButtonsAndGoTo palette={props.theme}>
                <div className="tab-buttons">
                  {displayButtons(layerName, obj, props.arrestIsChecked)}
                </div>
                <ReactTooltip
                  id={`go-to-${i}`}
                  delayShow={250}
                  type={props.dark ? "light" : "dark"}
                >
                    
                </ReactTooltip>
                <svg
                  data-tip
                  data-for={`go-to-${i}`}
                  onClick={() =>
                    props.handleSelect(
                      new WKT().readGeometryFromText(obj.geometry)
                    )
                  }
                  xmlns="http://www.w3.org/2000/svg"
                  width="24"
                  height="24"
                  viewBox="0 0 24 24"
                >
                  <path d="M9 5v2h6.59L4 18.59 5.41 20 17 8.41V15h2V5z" />
                </svg>
              </ButtonsAndGoTo>
              {(layerName === "lands" || layerName === "buildings") &&
              obj.properties.passport_exists ? (
                <Download palette={props.theme} download={true}>
                  <p></p>
                  <a
                    href={`${window.REST_URL}/map/passport?cadnum=${obj.properties.kad_nomer}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    data-tip
                    data-for={`download-tooltip-${i}`}
                  >
                    <ReactTooltip
                      delayShow={250}
                      type={props.dark ? "light" : "dark"}
                      id={`download-tooltip-${i}`}
                    >
                      
                    </ReactTooltip>
                     
                  </a>
                </Download>
              ) : layerName === "lands" || layerName === "buildings" ? (
                <p className="passport">  </p>
              ) : null}
              {getTabContent(obj.properties, layerName, obj)}
            </Tabs>
          )}
        </Collapsible>
      </CollapsibleContainer>
    );
  }
  return (
    <FeaturesWrapper palette={props.theme}>
      <div id="container">
        {Object.keys(props.info).map(layer =>
          props.info[layer].map((obj, i) => createObject(layer, obj, i))
        )}
      </div>
      {props.getObjects.func && (
        <PageNav getObjects={props.getObjects} theme={props.theme} />
      )}
    </FeaturesWrapper>
  );
}

export default ObjectsInfoTable;
