const OSKEMEN = "05085";
const SEMEI = "05252";

const getPdpSublayers = region => {
  switch (region.regionCode + region.code) {
    case OSKEMEN:
      return getRegionSublayers(UK, "ukPDP");
    case SEMEI:
      return getRegionSublayers(SEM, "semPDP");
    default:
      return [];
  }
};

export default getPdpSublayers;

const getRegionSublayers = (regionLayers, key) =>
  regionLayers.map((layer, index) => ({
    name: `${key + index}`,
    title: layer
  }));

